<template>
	<div class="">
		<div class="panel panel-default">
			<div class="panel-heading">
				<h3 class="panel-title">
                ToList ({{total}})
            </h3>
			</div>
			<div class="panel-body">
				<ul class="list-group">
					<li v-bind:class="{done:item.completed}" v-for="(item,index) in todo" class="list-group-item">
						{{item.id}}{{item.title}}-----{{item.completed}}
						<button v-bind:class="item.completed?'btn-danger':'btn-success'" class="btn pull-right btn-xs" @click="toggleState(index)">{{item.completed?'已完成':'完成'}}</button>
						<button class="btn btn-info btn-xs pull-right" @click="deleteItem(index)">删除</button>
					</li>
				</ul>
				<div class="form-group">
					<input type="text" class="form-control" v-model="newData.title" />
				</div>
				<div class="form-group">
					<button class="btn btn-success" @click="addNews(newData)">添加</button>
				</div>
			</div>
			<div class="panel-footer">

			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'hello',
		data() {
			return {
				newData: {
					id: null,
					title: "",
					completed: false
				},
				todo: [{
					id: 1,
					title: "吃饭",
					completed: false
				}, {
					id: 2,
					title: "睡觉",
					completed: false
				}, {
					id: 3,
					title: "做梦都会了",
					completed: false
				}, {
					id: 4,
					title: "发现想多了",
					completed: false
				}, {
					id: 5,
					title: "踏实练习",
					completed: false
				}]
			}
		},
		methods: {
			addNews(data) {
				this.todo.push(data)
				this.newData = {
					id: null,
					title: ""
				}
			},
			toggleState(index) {
				this.todo[index].completed = !this.todo[index].completed
				console.log(index)
			},
			deleteItem(index) {
				this.todo.splice(index, 1)
			}
		},
		computed: {
			total() {
				return this.todo.length
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.done {
		color: red;
		text-decoration: line-through;
	}
</style>


