<template>
	<div class="hello">
		<!--数据的显示:表达式和 v-text-->
		{{message}}
		<!--双向数据绑定 v-model-->
		<input type="text" v-model="message" />
		<span v-text="info"></span>
		<!--循环数组显示数据-->
		<ul>
			<!--<li v-for="v in list">{{v.id}}-&#45;&#45;{{v.title}}</li>-->
			<li v-for="(v,index) in list">{{index}}---{{v.id}}---{{v.title}}</li>
		</ul>
		<!--触发事件-->
		<button v-on:click="go()">点击</button>
		<button @click="run()">重击</button>
	</div>
</template>

<script>
	export default {
		name: 'hello',
		data() {
			return {
				//数据
				message: 'hello world',
				info: '我是Vue',
				list: [{
					id: 1,
					title: "天涯海阁"
				}, {
					id: 2,
					title: "小圣贤庄"
				}, {
					id: 3,
					title: "御剑山庄"
				}, {
					id: 4,
					title: "龙泽山庄"
				}, {
					id: 5,
					title: "天涯海阁"
				}]
			}
		},
		methods: {
			go() {
				console.log("Go! Go! Go!")
			},
			run() {
				console.log("run")
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


